import React, { Component } from 'react';
import { connect } from 'react-redux'
import Swal from 'sweetalert2'
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

import { loadData } from '../store/actions/dataAction.js'
import { storageService } from '../services/session-storage.js';

class _Logout extends Component {


    state = { isEn: true }

    componentDidMount = async () => {
        await this.props.loadData()
        const currUser = storageService.load('currUser')
        this.setState({ isEn: currUser?.game?.lang === 'English' ? true : false })
    }

    logout = () => {
        const { isEn } = this.state
        Swal.fire({
            title: isEn ? 'Are Your Sure You Want To Log Out?' : '?בטוח',
            showCancelButton: true,
            focusCancel: true,
            confirmButtonText: isEn ? 'Yes' : 'כן',
            confirmButtonColor: '#ff955a',
            reverseButtons: true,
            allowOutsideClick: false,
            cancelButtonText: isEn ? 'Cancel' : 'ביטול'
        }).then(result => {
            if (result.isConfirmed) {
                this.props.logout()
                this.props.history.push("/")
            }
        })
    }
    render() {
        const { isEn } = this.state
        return (
            <div className="logout a-center ttc j-center flex" onClick={this.logout}>
                <p>{`${isEn ? 'Log out' : 'התנתק/י'}`}<span><ExitToAppIcon /></span></p>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
    }
}
const mapDispatchToProps = {
    loadData
}
export const Logout = connect(mapStateToProps, mapDispatchToProps)(_Logout)