import React, { Component } from "react";
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { dataService } from "../services/dataService";
import { storageService } from "../services/session-storage";


function _About(props) {
    const { t } = useTranslation()
    // var isEn = dataService.loadData().then(data => {
    //     // if (!data.user.nickname) props.history.push('/')
    //     return data?.game?.lang === 'English' ? true : false
    // }).then(e => isEn = e);
    var currUser = storageService.load('currUser')
    var lang = currUser ? currUser?.game?.lang : 'English'
    var isEn = lang === 'English' ? true : false
    return (
        <section className="app-about ttc pf left-trans"
            style={{ fontFamily: isEn ? 'montserrat' : 'sans-serif' }}
        >
            <header>
                <p
                    style={{ letterSpacing: isEn ? '1.3vw' : '' }}
                >{t('a-welcome')}</p>
                <small>{t('a-small')}</small>
            </header>
            <div>{t('a-div')}</div>
            <footer>
                <p>{t('a-wish')}</p>
            </footer>
            <footer>
                <Link to="/settings">
                    <p>{t('a-lnk-s')}</p>
                </Link>
                <Link to='/play'>
                    <p>{t('a-lnk-p')}</p>
                </Link>
            </footer>
        </section>
    )

}
const mapStateToProps = state => {
    return {
        // lang: state.dataModule.data.game.lang
    }
}
const mapDispatchToProps = {
}
export const About = connect(mapStateToProps, mapDispatchToProps)(_About)