import React, { Component } from 'react'
import { connect } from 'react-redux'
import { NavLink, withRouter } from 'react-router-dom'

import { Settings } from '../pages/Settings'
import { Logout } from './Logout'
import { Home } from '../pages/Home';


import avatarSrc from '../assets/imgs/user.png'
import logoSrc from '../assets/imgs/bookLogo.png'

import MenuIcon from '@material-ui/icons/Menu';
import SettingsIcon from '@material-ui/icons/Settings';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';

//functions:
import { logout } from '../store/actions/userAction.js'
import { loadData } from '../store/actions/dataAction'


import clock from '../assets/imgs/time.png'
import finish from '../assets/imgs/finish.png'
import round from '../assets/imgs/round.png'
import { storageService } from '../services/session-storage'

class _AppHeader extends Component {

    state = {
        className: 'menu',
        menuOptions: [{ txt: 'home', to: 'home' }, { txt: 'adding', to: 'adding' }, { txt: 'dashboard', to: 'dashboard' }, { txt: 'settings', to: 'settings' }, { txt: 'about', to: 'about' }],
        sideMenuOptions: [{ txt: 'home', to: 'about' }, { txt: 'editor', to: 'adding' }, { txt: 'dashboard', to: 'dashboard' }],
        isSettingsOpen: false,
        isProfileOpen: false,
        currUser: {}
    }

    async componentDidMount() {
        await this.props.loadData()
        const { data, history } = this.props
        const currUser = storageService.load('currUser')
        if (!currUser?.nickname) history.push("/")
        this.setState({ currUser: { ...currUser } })
    }

    get navForDisplay() {
        return this.state.currUser?.game?.lang === 'English' ? [{ txt: 'home', to: 'about' }, { txt: 'editor', to: 'adding' }, { txt: 'dashboard', to: 'dashboard' }] : [{ txt: 'בית', to: 'about' }, { txt: 'עריכה', to: 'adding' }, { txt: 'סטטיסטיקה', to: 'dashboard' }]
    }


    updateClass = () => this.setState({ className: this.state.className === 'menu' ? 'menu open' : 'menu' })

    render() {
        const { className, menuOptions, isSettingsOpen, isProfileOpen, sideMenuOptions, currUser } = this.state
        const { data, history, logout } = this.props
        const { nickname, image, game } = currUser
        var isEn = game?.lang === 'English' ? true : false
        const navOps = this.navForDisplay
        return (
            <React.Fragment>
                <header className="app-header pf">
                    <div className="inner flex">
                        <div className="flex j-between">
                            <img src={logoSrc} alt="" />
                            <p>trinius</p>
                        </div>
                        <div className="wider-menu flex">
                            {navOps.map((o, idx) => <NavLink to={`/${o.to}`} exact={true} activeClassName="active-link" key={idx}>{o.txt}</NavLink>)}
                            <p id={`${isSettingsOpen ? 'settings' : ''}`} onClick={() => this.setState({ isSettingsOpen: !this.state.isSettingsOpen })}>{isEn ? 'Settings' : 'הגדרות'}<span><SettingsIcon /></span></p>
                            <Logout logout={logout} data={data} history={history} />
                        </div>
                        <MenuIcon id={`${className === 'menu open' ? 'menu-icon' : ''}`} className="menu-icon pa" onClick={() => this.setState({ className: className === 'menu' ? 'menu open' : 'menu' })} alt="" />

                        <div className="backImgs">
                            <img src={clock} alt="" />
                            <img src={round} alt="" />
                            <img src={finish} alt="" />
                        </div>
                    </div>
                    <section id="app-menu" className={className} onBlur={(ev) => {
                        ev.stopPropagation()
                        this.setState({ className: 'menu' })
                    }}>
                        <div className="little-profile">
                            <img src={image ? image?.src : avatarSrc} style={{ backgroundColor: image?.bgClr }} alt="" className="flex" />
                            <p className="tac">{nickname}</p>
                        </div>
                        <div className="menu-options flex column">
                            {menuOptions.map((o, idx) => <NavLink to={`/${o.to}`} onClick={() => this.setState({ className: 'menu' })} exact={true} activeClassName="active-menu-link" key={idx}>{o.txt}</NavLink>)}
                            <img src={logoSrc} alt="" />
                        </div>
                    </section>

                </header>

                <section id="menu" className={`settings-menu ${isSettingsOpen ? 'open' : ''}`}><Settings data={data} isOnDesktop={true} /></section>

                <div className="left-area">

                    <img src={clock} alt="" />
                    <img src={round} alt="" />
                    <img src={finish} alt="" />
                    <img src={clock} alt="" />
                    <img src={round} alt="" />
                    <img src={finish} alt="" />
                    <img src={clock} alt="" />
                    <img src={round} alt="" />
                    <img src={finish} alt="" />
                    <img src={clock} alt="" />
                    <img src={round} alt="" />
                    <img src={finish} alt="" />
                    <div className="profile flex a-center j-center pa ttc tac" onClick={() => this.setState({ isProfileOpen: !this.state.isProfileOpen })}>
                        <p>{isEn ? 'Your Profile' : 'פרופיל'}</p>
                        <ArrowRightIcon />
                    </div>
                </div>

                <div className={`back-dark ${isProfileOpen ? 'shown' : ''}`}> </div>
                <div className={`profile-div ${isProfileOpen ? 'open' : ''}`}>  <Home data={data} isOnDesktop={true} /></div>

            </React.Fragment >
        )
    }
}
const mapStateToProps = state => {
    return {
        data: state.dataModule.data
    }
}
const mapDispatchToProps = {
    loadData,
    logout
}
export const AppHeader = withRouter(connect(mapStateToProps, mapDispatchToProps)(_AppHeader));